.services-advantage {
    background: none;
    padding-bottom: 78px;


    &__group {
        margin: 0 -20px;
        flex-wrap: wrap;

        & .group-item {
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;
            align-items: center;

            &__text-inset {
                width: 861px;

            }

            &__img {
                padding: 14px;
                background-color: $white;
                width: 92px;
                height: 92px;
                border-radius: 24px;
                vertical-align: middle;
                margin-bottom: 20px;
                margin-top: -28px;
            }

            &__title {
                font-size: 42px;
                letter-spacing: -1.3px;
                line-height: 50px;
                text-align: center;
                color: $magenta;
            }

            &__text {
                font-size: 21px;
                line-height: 30px;
                color: $white;
                text-align: center;
                padding-top: 20px;

            }
        }

    }

    &__btn-wrap {
        margin-top: 40px;
        display: flex;
        justify-content: center;
    }

    &__btn {
        cursor: pointer;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        color: #FFFFFF;
        border: none;
        padding: 16px 40px 17px;
        background: #24A3FF;
        box-shadow: 0px 0px 20px rgba(36, 163, 255, 0.5);
        border-radius: 12px;

        &:hover {
            background-color: #0085FF;
        }

        &:active {
            background-color: #006CCF;
        }


    }

    form {
        overflow: hidden;
        margin: 0 auto;
        padding: 20px;
        min-width: 320px;
        max-width: 527px;
        width: 100%;
        background: #F5F5FA;
        border-radius: 12px;
        filter: drop-shadow(0px 15px 80px rgba(40, 40, 86, 0.5));
    }

    textarea, input {
        display:block;
        margin-bottom:20px;
        padding-left: 10px;
        padding-right:10px;
        border-radius: 8px;
        background-clip: padding-box;
        font-size: 18px;
        line-height: 26px;
        color: #000000;

        font-family: "TT Norms", Arial, sans-serif;

    }

    textarea{
        overflow:hidden;
        width: 100%;
        height: 180px;
        border-style: none;
    }

    input:not(.btn-send) {
        width: 100%;
        height: 47px;
        border-style: none;
        font-size: 18px;
        line-height: 26px;
        color: #000000;
    }
    input[type=submit] {
        cursor:pointer;
    }
    input:focus, textarea:focus {
        background: #FFFFFF;
        border-radius: 8px;
        overflow: hidden;
    }

    :focus::placeholder {
        color:transparent;
    }

    .btn-close {
        cursor: pointer;
        border: none;
        background-image: url(../../img/close-icon.svg);
        background-repeat: no-repeat;
        background-color: transparent;
        height: 32px;
        width: 32px;
        position: absolute;
        top: 15px;
        right: 15px;
}

    .help-title {
        font-size: 32px;
        font-weight: 700;
        line-height: 38px;
        color: $magenta;
        padding-bottom: 20px;
    }
    .help-text {
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 1px;
        font-weight: 500;
        text-transform: uppercase;
        color: $text;
        margin-bottom: 9px;
    }

    .btn-send {
        border-style: none;
        background: #24A3FF;
        box-shadow: 0px 0px 20px rgba(36, 163, 255, 0.5);
        border-radius: 12px;
        font-size: 14px;
        font-weight: 500;
        line-height: 17px;
        text-align: center;
        color: $white;
        outline: none;
        padding: 16px 72px 17px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 0;

        &:hover {
            background-color: #0085FF;
        }

        &:active {
            background-color: #006CCF;
        }
    }

    .overlay {
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 10000;
        visibility: hidden;
        background-color: rgba($color:#323264, $alpha: 0.7);
        position: fixed;
        transition: opacity .5s;

        }
        .overlay:target {
        visibility: visible;
        opacity: 1;
        }

        .modal {
         right: 0;
         left: 0;
         width: 50%;
         z-index: 10001;
         opacity: 0;
         display: block;
         visibility: hidden;
         position: absolute;
         transition: opacity 500ms ease-in;
        }

        .overlay:target+.modal{
        top: 25%;
        visibility: visible;
        opacity: 1;
        }
        /*  */
        @media only screen and (min-width: 768px) and (max-width: 991px) {
           .modal {
               width: 95%;
            }
        }
        /*  */
        @media only screen and (min-width: 375px) and (max-width: 767px) {
           .modal {
               width:85%;
            }
        }

}
