.footer {
    padding: 29px 0 40px;
    background-color: #F5F5FA;

    & .footer-wrap {
        display: flex;
        justify-content: space-between;

        .footer-left {
            align-self: flex-start;
            display: flex;
            flex-direction: column;
            width: 41.67%;

                & a:not(.footer-left__logo-fb)::before {
                    content: "";
                    width: 6px;
                    height: 6px;
                    border-radius: 50%;
                    background-color: rgba($color: #B3B3BA, $alpha: 0.7);
                    display: inline-block;
                    vertical-align: 2px;
                    margin-right: 10px;
                }

                &__contact {
                    display: flex;
                    padding-top: 31px;
                }

                &__text {
                    padding-right: 32px;
                }

                &__map {
                    text-transform: uppercase;
                    font-weight: 700;
                    font-size: 12px;
                    line-height: 14px;
                    letter-spacing: 1px;
                    color: $blue;

                    &:hover {
                        color: $magenta;
                    }
                }



                &__tel a {
                    padding-right: 10px;
                    font-weight: 700;
                    font-size: 16px;
                    line-height: 19px;
                    color: $dark-violet;

                    &:hover {
                        color: $magenta;
                    }
                }

                &__address {
                    display: flex;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 19px;
                    color: $navy;
                }

                &__mail a {
                    font-weight: 700;
                    font-size: 16px;
                    line-height: 19px;
                    letter-spacing: 1px;
                    color: $blue;

                    &:hover {
                        color: $magenta;
                    }
                }

                &__logo {
                    margin-top: -9px;
                    padding-right: 10px;


                    & path:hover {
                        fill: $magenta;
                    }
                }


            }
        }

    .footer-right {

        &__text {
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            text-align: right;
            color: #B3B3BA;
        }

    }
}

@media (max-width: 767px) {}

@media (max-width: 375px) {
    .footer {
        padding-bottom: 118px;

        & .footer-wrap {
            flex-direction: column;
            justify-content: center;
            align-items: center;


            .footer-left {
                width: 100%;
                flex-direction: column;
                align-items: center;
                justify-content: center;

                & a:not(.footer-left__logo-fb)::before {
                    background-color: transparent;
                }
                &__text {
                    padding-right: 0;
                    margin-bottom: 10px;
                }

                &__address {
                    width: 100%;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                }

                &__contact {
                width: 100%;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                padding-top: 25px;
                }
                &__tel {
                    padding: 20px 0;
                }

                &__mail {
                    padding-bottom: 20px;
                }
            }
        }
        & .footer-right__text {
            width: 320px;
            text-align: center;
        }
}
}
